function CheckForm({ formData }) {
  // 校验 platform_id 和 env_id 是否有值（非空字符串）
  if (!formData.platform_id || !formData.env_id) {
    return new Error("platform_id 和 env_id 必须有值");
  }

  // 校验 player_id 是否有值且是大于0的数字
  if (
    !formData.player_id ||
    !checkNumber(formData.player_id) ||
    parseInt(formData.player_id) <= 0
  ) {
    return new Error("player_id 必须有值且是大于0的数字");
  }

  // 校验至少有一个 chip、vip、nick、sex、portrait、unlock、other_id、other_value 有值
  if (
    !formData.chip &&
    !formData.vip &&
    !formData.nick &&
    !formData.sex &&
    !formData.portrait &&
    !formData.unlock &&
    !formData.other_id &&
    !formData.other_value
  ) {
    return new Error(
      "chip、vip、nick、sex、portrait、unlock、other_id、other_value 必须至少有一个值"
    );
  }

  // 校验 vip 是否有值且是 0~30 之间的数字
  if (
    formData.vip &&
    (!checkNumber(formData.vip) ||
      parseInt(formData.vip) < 0 ||
      parseInt(formData.vip) > 30)
  ) {
    return new Error("vip 必须是 0~30 之间的数字");
  }

  // 校验 chip 是否有值且是大于0的数字
  if (
    !formData.chip ||
    !checkNumber(formData.chip) ||
    parseInt(formData.chip) <= 0
  ) {
    return new Error("chip 必须有值且是大于0的数字");
  }

  // 校验 other_id 是否是大于0的数字
  if (
    formData.other_id &&
    (!checkNumber(formData.other_id) || parseInt(formData.other_id) <= 0)
  ) {
    return new Error("other_id 必须是大于0的数字");
  }

  // 校验 other_value 是否是数字且大于0，并且当 other_id 为 1 时必须在 0~30 之间
  if (
    formData.other_value &&
    (!checkNumber(formData.other_value) || parseInt(formData.other_value) <= 0)
  ) {
    return new Error("other_value 必须是大于0的数字");
  }
  if (
    parseInt(formData.other_id) === 1 &&
    (parseInt(formData.other_value) < 0 || parseInt(formData.other_value) > 30)
  ) {
    return new Error("当 other_id 为 1 时，other_value 必须在 0~30 之间");
  }

  // 校验 other_id 和 other_value 的依赖关系
  if (
    (formData.other_id && !formData.other_value) ||
    (!formData.other_id && formData.other_value)
  ) {
    return new Error("other_id 和 other_value 必须同时有值或同时没有值");
  }

  return null; // 表示没有错误
}

function checkNumber(value) {
  return /^\d+$/.test(value);
}

// 测试例子
const formData1 = {
  platform_id: "1",
  env_id: "2",
  player_id: "123",
  vip: "10",
  chip: "100",
  nick: "John",
  sex: "Male",
  portrait: "image.jpg",
  unlock: "yes",
  other_id: "",
  other_value: "",
};

const formData2 = {
  platform_id: "1",
  env_id: "2",
  player_id: "123",
  vip: "10",
  chip: "100",
  nick: "John",
  sex: "Male",
  portrait: "image.jpg",
  unlock: "yes",
  other_id: "1",
  other_value: "20",
};

const formData3 = {
  platform_id: "1",
  env_id: "2",
  player_id: "123",
  vip: "10",
  chip: "100",
  nick: "John",
  sex: "Male",
  portrait: "image.jpg",
  unlock: "yes",
  other_id: "1",
  other_value: "40", // 这会触发错误
};

console.log(CheckForm({ formData: formData1 })); // null，表示没有错误
console.log(CheckForm({ formData: formData2 })); // null，表示没有错误
console.log(CheckForm({ formData: formData3 })); // Error: 当 other_id 为 1 时，other_value 必须在 0~30 之间

module.exports = {
  CheckForm,
};
