import React, { useState, useEffect } from "react";
import { useLocation, Link, useNavigate } from "react-router-dom";
import { routes } from "./Routes";
import "../css/Sidebar.css";

function Sidebar() {
  const location = useLocation();
  const navigate = useNavigate();
  const [expandedPaths, setExpandedPaths] = useState([]);

  useEffect(() => {
    const currentPath = location.pathname;
    setExpandedPaths((prevExpandedPaths) => {
      const newExpandedPaths = [...prevExpandedPaths];
      if (!newExpandedPaths.includes(currentPath)) {
        newExpandedPaths.push(currentPath);
      }
      return newExpandedPaths;
    });
  }, [location.pathname]);

  const toggleExpandedPath = (path) => {
    let newExpandedPaths = [...expandedPaths];
    if (newExpandedPaths.includes(path)) {
      newExpandedPaths = newExpandedPaths.filter((p) => p !== path);
    } else {
      newExpandedPaths.push(path);
    }
    setExpandedPaths(newExpandedPaths);
  };

  const handleLinkClick = (path, hasChildren) => {
    navigate(path);
    if (hasChildren) {
      toggleExpandedPath(path);
    }
  };

  const renderLinks = (routes, level = 0) => (
    <ul className={`level-${level}`}>
      {routes.map((route) => (
        <li key={route.path}>
          <div
            className={`link-container level-${level} ${
              location.pathname === route.path ? "active" : ""
            }`}
            onClick={() => handleLinkClick(route.path, route.children)}
          >
            <Link
              to={route.path}
              onClick={(e) => {
                e.preventDefault();
                handleLinkClick(route.path, route.children);
              }}
            >
              {route.label}
            </Link>
            {route.children && (
              <button
                className="toggle-button"
                onClick={(e) => {
                  e.stopPropagation();
                  toggleExpandedPath(route.path);
                }}
              >
                {expandedPaths.includes(route.path) ? "-" : "+"}
              </button>
            )}
          </div>
          {route.children && expandedPaths.includes(route.path) && (
            <div
              className={`submenu ${
                expandedPaths.includes(route.path) ? "expanded" : ""
              }`}
            >
              {renderLinks(route.children, level + 1)}
            </div>
          )}
        </li>
      ))}
    </ul>
  );

  return <aside className="sidebar">{renderLinks(routes)}</aside>;
}

export default Sidebar;
