import React, { useState, useEffect } from "react";
import apiClient from "../api/apiClient";
import "../css/UpdateProp.css";
const { formDataCheck } = require("../utils/utils-exports");

function UpdateProp() {
  const [platforms, setPlatforms] = useState([]);
  const [envInfo, setEnvInfo] = useState([[]]);
  const [selectedPlatform, setSelectedPlatform] = useState("");
  const [formData, setFormData] = useState({
    platform_id: "",
    env_id: "",
    player_id: "",
    vip: "",
    chip: "",
    nick: "",
    sex: "",
    portrait: "",
    unlock: "",
    other_id: "",
    other_value: "",
  });

  useEffect(() => {
    async function fetchConfig() {
      try {
        const redisConfig = await apiClient.get("/get_redis");
        const { Platform, EnvInfo } = redisConfig;
        setPlatforms(Platform);
        setEnvInfo(EnvInfo);
      } catch (err) {
        console.error("Error:", err);
      }
    }

    fetchConfig().then((r) => {});
  }, []);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handlePlatformChange = (e) => {
    const platform = e.target.value;
    setSelectedPlatform(platform);
    setFormData({ ...formData, platform_id: platform, env_id: "" });
  };

  const handleEnvChange = (e) => {
    const env = e.target.value;
    setFormData({ ...formData, env_id: env });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      let error = formDataCheck.CheckForm({ formData });
      if (error !== null) {
        alert(error.message);
        return;
      }
      const response = await apiClient.post("/update_info", formData); // 替换为你的API路径
      let data = {
        IMEI: "",
        PlayerId: "",
        message: "",
      };
      data = response;
      const message = `修改道具OK \n设备号:${data.IMEI}\n玩家ID:${data.PlayerId}\nresponse:${data.message}`;
      alert(message);
      console.log("Response:", response);
    } catch (error) {
      console.error("Error:", error.message);
      alert(`修改道具失败\n${error.message}`);
    }
  };

  return (
    <div className="update-prop-container">
      <div className="update-prop-content">
        <h1>Prop Modification Tool</h1>
        <form id="updateRedis" onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="platform">Select:</label>
            <select
              name="platform_id"
              id="platform_id"
              value={formData.platform_id}
              onChange={handlePlatformChange}
            >
              <option value="">Select Platform</option>
              {platforms.map((platform, index) => (
                <option key={index} value={platform}>
                  {platform}
                </option>
              ))}
            </select>
            {selectedPlatform && (
              <select
                name="env_id"
                id="env_id"
                value={formData.env_id}
                onChange={handleEnvChange}
              >
                <option value="">Environment</option>
                {envInfo[platforms.indexOf(selectedPlatform)]?.map(
                  (env, index) => (
                    <option key={index} value={env}>
                      {env}
                    </option>
                  )
                )}
              </select>
            )}
          </div>

          <div className="form-group">
            <label htmlFor="player">Player ID:</label>
            <input
              type="text"
              id="player_id"
              name="player_id"
              value={formData.player_id}
              onChange={handleChange}
              placeholder="Required"
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="vip">VIP:</label>
            <input
              type="text"
              id="vip"
              name="vip"
              value={formData.vip}
              onChange={handleChange}
              placeholder="Must be 1-30 (Need to log in again)"
            />
          </div>

          <div className="form-group">
            <label htmlFor="chip">Chip:</label>
            <input
              type="text"
              id="chip"
              name="chip"
              value={formData.chip}
              onChange={handleChange}
            />
          </div>

          <div className="form-group">
            <label htmlFor="nick">Nick:</label>
            <input
              type="text"
              id="nick"
              name="nick"
              value={formData.nick}
              onChange={handleChange}
            />
          </div>

          <div className="form-group radio-group">
            <label htmlFor="sex">Sex:</label>
            <label className="radio-label" htmlFor="s_male">
              <input
                type="radio"
                id="s_male"
                name="sex"
                value="S_Male"
                checked={formData.sex === "S_Male"}
                onChange={handleChange}
              />
              Male
            </label>
            <label className="radio-label" htmlFor="s_female">
              <input
                type="radio"
                id="s_female"
                name="sex"
                value="S_Female"
                checked={formData.sex === "S_Female"}
                onChange={handleChange}
              />
              Female
            </label>
          </div>

          <div className="form-group">
            <label htmlFor="portrait">Portrait:</label>
            <input
              type="text"
              id="portrait"
              name="portrait"
              value={formData.portrait}
              onChange={handleChange}
              placeholder="Example (https://*)"
            />
          </div>

          <div className="form-group radio-group">
            <label htmlFor="unlock">Unlock:</label>
            <label className="radio-label" htmlFor="unlock-yes">
              <input
                type="radio"
                id="unlock-yes"
                name="unlock"
                value="yes"
                checked={formData.unlock === "yes"}
                onChange={handleChange}
              />
              Yes
            </label>
            <label className="radio-label" htmlFor="unlock-no">
              <input
                type="radio"
                id="unlock-no"
                name="unlock"
                value="no"
                checked={formData.unlock === "no"}
                onChange={handleChange}
              />
              No
            </label>
          </div>

          <div className="form-group">
            <label htmlFor="other_id"> Other ID:</label>
            <input
              type="text"
              id="other_id"
              name="other_id"
              value={formData.other_id}
              onChange={handleChange}
              placeholder="PropId"
            />
          </div>

          <div className="form-group">
            <label htmlFor="other_value">Other Value:</label>
            <input
              type="text"
              id="other_value"
              name="other_value"
              value={formData.other_value}
              onChange={handleChange}
              placeholder="PropIdNumber"
            />
          </div>

          <input type="submit" value="Update" />
        </form>
      </div>
    </div>
  );
}

export default UpdateProp;
